import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('../views/Home')
  },
  {
    path: '/games',
    name: 'games',
    component: () => import('../views/Games')
  },
  {
    path: '/demus',
    name: 'demus',
    component: () => import('../views/Demus')
  },
  {
    path: '/*',
    name: 'notFound',
    component: () => import('../views/NotFound')
  },
]

const router = new VueRouter({
  routes,
  mode: 'history'
})

export default router